.bellAnimation {
  -webkit-animation-name: bell-resizing;
          animation-name: bell-resizing;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  }

@-webkit-keyframes bell-resizing {
  0% {height: 12%; width: 12%; left: 18.8vh}
  90% {height: 12%; width: 12%; left: 18.8vh}
  93% {height: 15%; width: 15%; left: 43%}
  94% {height: 12%; width: 12%; left: 18.8vh}
  97% {height: 15%; width: 15%; left: 43%}
  100% {height: 12%; width: 12%; left: 18.8vh}
}

@keyframes bell-resizing {
  0% {height: 12%; width: 12%; left: 18.8vh}
  90% {height: 12%; width: 12%; left: 18.8vh}
  93% {height: 15%; width: 15%; left: 43%}
  94% {height: 12%; width: 12%; left: 18.8vh}
